import axios from "axios";
import {apiHost} from "../config";
import {normalize, schema} from "normalizr";

const tokenHeaderName = 'authorization'

const server = axios.create(
    {
        baseURL: apiHost,
    }
);

const handle_response = (response) => {
    //        
    //         
    //  token 
    if (response === undefined) {
        console.log('Response without headers')
        return response
    }
    console.log('response', response)
    console.log('headers', response.headers)
    const token = response.headers[tokenHeaderName];
    if (!!token) {
        window.localStorage.setItem("token", token)
    }
    return response
}


//  .       
server.interceptors.request.use(function (config) {
    config.headers['authorization'] = String(window.localStorage.getItem("token"));
    return config;
}, null);

server.interceptors.response.use(function (response) {

    return handle_response(response);
}, function (error) {
    return Promise.reject(error);
});

const normalizeSelectiveQuery = (responseJson) => {
    const entity = new schema.Entity('entities');
    const cluster = new schema.Entity('clusters', {
        'entities': {
            'items': [entity]
        }
    });
    const mySchema = {
        'clusters': {
            'items': [cluster]
        }
    };
    return normalize(responseJson, mySchema)
};

export const executeSelectiveQuery = (queryString) => (
    server.get(`/query/?q=${queryString}`).then(response => {
            return normalizeSelectiveQuery(response.data.data)
        }
    )
);

export const authorizationCheckQuery = () => {
    return server.get(`/auth/vk/check/`).then(response => {
            return response.data
        }
    )
};

export const initialQuery = () => {
    return server.get(`/auth/init/`).then(response => {
            return response.data.data
        }
    )
};


