import React, {PureComponent, ReactChild, Component} from 'react'


export default class OauthPopup extends Component {

    static defaultProps = {
        onClose: () => {
        },
        width: 500,
        height: 500,
        url: "",
        title: ""
    };

    componentWillUnmount() {
        if (this.externalWindow) {
            this.externalWindow.close();
        }
    }

    onClose = () => {
        this.props.onClose()
    }

    createPopup = () => {
        const {url, title, width, height, onCode} = this.props;
        const left = window.screenX + (window.outerWidth - width) / 2;
        const top = window.screenY + (window.outerHeight - height) / 2.5;

        const windowFeatures = `toolbar=0,scrollbars=1,status=1,resizable=0,location=1,menuBar=0,width=${
            width},height=${height},top=${top},left=${left}`;

        this.externalWindow = window.open(
            url,
            title,
            windowFeatures
        );
        const checkTimeoutMs = 100;
        const checkWindowStatus = () => {
            if (this.externalWindow.closed) {
                console.log('WINDOW CLOSED')
                this.onClose()
            } else {
                setTimeout(checkWindowStatus, checkWindowStatus)
            }
        }

        setTimeout(checkWindowStatus, checkTimeoutMs)


        const storageListener = () => {
            try {
                if (localStorage.getItem('code')) {
                    onCode(localStorage.getItem('code'));
                    this.externalWindow.close();
                    window.removeEventListener('storage', storageListener);
                }
            } catch (e) {
                window.removeEventListener('storage', storageListener);
            }
        }

        window.addEventListener('storage', storageListener);

        // this.externalWindow.addEventListener('beforeunload', () => {
        //     this.props.onClose()
        // }, false);
    };

    componentDidMount() {
        this.createPopup()
    }

    render() {
        return <></>
    }
}
