import React from 'react';
import style from './style.less'


const Link = (props) => {
    const {text, url, style} = props;
    return (
        <a
            style={style}
            onMouseDown={e=>e.stopPropagation()}
            onClick={e=>e.stopPropagation()}
            className="customLink"
            href={url}
            target="_blank"
            rel="noopener noreferrer"
        >
            <div>{text}</div>
        </a>
    )
};

export default Link;
