import React from "react";
import {Form, Rating} from "semantic-ui-react";


export const formInputComponent = (props) => {
    props = {...props};
    const submit = props.submitIcon;
    delete props['submitIcon'];
    const canSubmit = (props.input.value && submit);
    const searchIcon = {
        name: 'search', link: true, onClick: e => {
            e.preventDefault();
            submit()
        }
    };
    return (
        <Form.Input
            onKeyDown={e => {
                if (e.keyCode === 13) {
                    submit()
                }
            }}
            icon={canSubmit ? searchIcon : undefined}

            {...{
                ...props,
                ...props.input,
                error: props.meta.error,
                onChange: (e, data) => {
                    if (!data)
                        return props.input.onChange(e);
                    return props.input.onChange(data.value)
                }
            }}
        />
    )
};

export const formRadioComponent = (props) => {
    return (
        <Form.Radio
            {...{
                ...props,
                ...props.input,
                error: props.meta.error,
                onChange: (e, {checked}) => props.input.onChange(props.input.value)
            }}
        />
    )
};

export const formTextComponent = (props) => {
    props = {...props};
    return (
        <Form.TextArea
            {...{
                ...props,
                ...props.input,
                error: props.meta.error,
                onChange: (e, data) => {
                    return props.input.onChange(data.value)
                }
            }}
        />
    )
};

export const formRatingComponent = (props) => {
    props = {...props};
    console.log('RATING', props);
    return (
        <Rating
            style={{marginBottom: '1rem'}}
            onRate={(e, data) => {
                e.preventDefault();
                props.input.onChange(data.rating)
            }}
            clearable
            maxRating={5}

            {...{
                ...props,
                ...props.input,
            }}
        />
    )
};

const formComponentGenerator = (TargetComponent, submit) => {
    return (
        props => {
            // console.log('Form Wrapper props', props);
            let valueName = 'value';
            if (typeof props.input.value === 'boolean')
                valueName = 'checked';
            const canSubmit = (props.input.value && submit);
            const searchIcon = {
                name: 'search', link: true, onClick: e => {
                    e.preventDefault();
                    submit()
                }
            };
            return (
                <TargetComponent
                    onKeyDown={e => {
                        if (e.keyCode === 13) {
                            submit()
                        }
                    }}
                    icon={canSubmit ? searchIcon : undefined}

                    {...{
                        ...props,
                        ...props.input,
                        value: undefined,
                        [valueName]: props.input.value,
                        error: props.meta.error,
                        onChange: (e, data) => {
                            if (!data)
                                return props.input.onChange(e);
                            return props.input.onChange(data[valueName])
                        }
                    }}
                />
            )
        }
    )
};


export default formComponentGenerator;