import React, {Component} from "react";
import {Header, Menu} from "semantic-ui-react";
import Authorizing from "./Authorizing/Authorizing";
import {Button} from "../utils/UI";
import Feedback from "./Feedback";
import {authorizeAction, submitFeedback} from "../../store/actions";
import {connect} from "react-redux";
import VkAuth from 'react-vk-auth';
import OauthPopup from "./Authorizing/Login";
import {apiHost, vkAppID} from "../../config";
import {authorizationCheckQuery} from "../../api/api";

class PageHeader extends Component {

    checkAuthorized = () => {
        authorizationCheckQuery().then(
            data => {
                console.log('authorized', data);

                this.setState({
                    authorized: data.status === 'authorized'
                })
            }
        )
    }
    componentDidMount() {
        this.checkAuthorized()
    }

    state = {
        status: "base",
        authorized: false
    };

    authorizeMe = () => {
        console.log('AUTHORIZE ME BUTTON CLICKED')
        this.props.authorize()
    }

    render() {

        // TODO  -  .   
        const vkAuthURL = 'https://oauth.vk.com/authorize';
        const redirect_uri = `${apiHost}/auth/vk/`
        const scope = '1385477'
        //     
        //      (    )
        //         
        const state = window.localStorage.getItem("token")


        const url = `${vkAuthURL}?client_id=${vkAppID}&redirect_uri=${
            redirect_uri
        }&display=popup&scope=${scope}&response_type=code&state=${state}`
        return (
            <div>
                <Menu secondary>
                    <Menu.Item style={{padding: 0}}>
                        <Header as="h1">NTrail</Header>
                    </Menu.Item>

                    <Menu.Item position="right" style={{padding: 0}}>

                        <Button basic style={{marginRight: "0.5em"}} onClick={() => this.setState({status: 'feedback'})}
                                text={' '}/>
                        {/*<Button basic style={{marginRight: "0.5em"}} text={' ?'}/>*/}
                        {
                            this.state.authorized !== true &&
                            <Button basic onClick={() => this.setState({status: 'vkauth'})} text={''}/>
                        }
                        {this.state.status === "auth" && (
                            <Authorizing
                                handleClose={() => this.setState({status: "base"})}
                            />
                        )}
                        {
                            this.state.status === 'vkauth' && (
                                <OauthPopup url={url} title={''}
                                            onClose={() => {
                                                this.setState({status: 'base'});
                                                this.checkAuthorized()
                                            }}/>
                            )
                        }
                        {
                            this.state.status === 'feedback' &&
                            <Feedback handleClose={() => this.setState({status: "base"})}/>
                        }
                    </Menu.Item>
                </Menu>
                <p style={{fontSize: "1.23em"}}>    </p>
            </div>
        );
    }
}

const mapDispatchToProps = ({
    authorize: authorizeAction
});


const mapStateToProps = state => {
    return {}
};

export default connect(mapStateToProps, mapDispatchToProps)(PageHeader);


