import React from 'react';
import ClusterImages from "./ImagesGroup";
import {Icon} from "semantic-ui-react";
import {selectCluster} from "../../store/clustersReducer";
import {connect} from "react-redux";
import {getEntitiesColors, getSelectedItemsIDS} from "../../store/selectors";


const ClusterLine = (props) => {
    const {toggleOpen, cluster, opened, haveChildren, selected, selectCluster, indent} = props;
    const {name, count, photos, id} = cluster;
    let containerClasses = ['clusterLine'];
    if(props.selectedEntities.map(id=>props.entitiesColors[id].id).includes(id))
        containerClasses.push('highlightedClusterLine');
    if(selected)
        containerClasses.push('selectedClusterLine');
    return (
        <div
            style={{marginLeft: indent * 10 + 'px'}}
            className={containerClasses.join(' ')}
            onClick={e => {
                e.stopPropagation();
                selectCluster(cluster.id)
            }}
        >
            {
                haveChildren ?
                    <Icon style={{marginLeft: 'auto'}} onClick={e => {
                        e.stopPropagation();
                        toggleOpen()
                    }} link name={opened ? 'caret down' : 'caret right'}/>
                    : <div/>
            }
            <div className={'clusterName'}>{name}</div>
            <ClusterImages photos={photos}/>
            <div style={{margin: 'auto'}}>{count}</div>
        </div>
    )
};

const mapStateToProps = (state) => ({
    selectedEntities: getSelectedItemsIDS(state),
    entitiesColors: getEntitiesColors(state)
});

export default connect(mapStateToProps)(ClusterLine);
