import React from 'react';
import style from './uiStyle.less';
import {Segment} from "semantic-ui-react";

function getBrightness(hexcolor) {
    if(hexcolor[0] !== '#')
        return null;
    var r = parseInt(hexcolor.substr(1, 2), 16);
    var g = parseInt(hexcolor.substr(3, 2), 16);
    var b = parseInt(hexcolor.substr(4, 2), 16);
    var yiq = ((r * 299) + (g * 587) + (b * 114)) / 1000;
    // Return new color if to dark, else return the original
    return yiq;
}


export const Button = (props) => {
    let {text, onClick, style, compact, basic, primary, ...otherProps} = props;
    let padding = '10px 30px';
    if (!onClick)
        onClick = () => {
        };

    const additionClasses = [];
    if (props.compact) {
        padding = '5px 10px'
    }
    if(props.primary){
        additionClasses.push('customButtonPrimary')
    }else if(props.basic){
            additionClasses.push('customButtonBasic')
    }

    if(props.color){
        const brightness = getBrightness(props.color);
        let textColor = 'black';
        if (brightness && brightness < 100)
            textColor = 'white';
        style = {...style, backgroundColor: props.color, color: textColor}
    }

    style = {
        padding: padding,
        ...style
    };
    return (
        <button
            className={'customButton ' + additionClasses.join(' ')}
            style={style}
            onClick={e => {
                e.preventDefault();
                onClick(e)
            }}
            {...otherProps}
        >{text}</button>
    )
};

export const UIGroup = (props) => {
    return (
        <div className={'UIGroup'}>
            {
                props.children
            }
        </div>
    )
};


export const UISegment = props => {
    return <Segment {...props}/>
};
