import React from 'react';
import {Form} from "semantic-ui-react";
import {connect} from "react-redux";
import {getSelectedEntitiesURLS} from "../../../store/selectors";

const CopyPasteInput = ({value, label, component, condition}) => {
    if (condition === undefined)
        condition = true;
    if (!value || !condition)
        return <></>;
    let Component = Form.Input;
    if (component)
        Component = component;

    return <Component value={value} label={label}/>;
};


const ParamsPanel = (props) => {
    return (
        <Form style={{marginTop: '1rem'}}>
            <CopyPasteInput label={' '} value={props.version}/>
            <CopyPasteInput label={'ID  '} value={props.clusterID}/>
            <CopyPasteInput component={Form.TextArea} label={'ID  '}
                            value={props.selectedClustersID.join('\n')}/>

            <Form.Group widths={'2'}>
                <CopyPasteInput label={' '} value={props.clustersCount}/>
                <CopyPasteInput label={'  '} value={props.entitiesCount}/>
            </Form.Group>
            <Form.Group widths={'2'}>
                <CopyPasteInput label={'ID  '} value={props.selectedEntitiesID.join(', ')}/>
                <CopyPasteInput label={'-  '} value={props.selectedEntitiesID.length}/>
            </Form.Group>


            <CopyPasteInput component={Form.TextArea}
                            label={'   '}
                            value={props.selectedEntitiesURLS.join('\n')}/>
        </Form>
    )
};

const mapStateToProps = state => {
    return {
        clusterID: state.workSpace.clusters.selectedClusterID,
        version: state.workSpace.version,
        selectedEntitiesID: state.workSpace.entities.selectedItems,
        selectedEntitiesURLS: getSelectedEntitiesURLS(state),
        clustersCount: state.workSpace.clusters.items.length,
        entitiesCount: state.workSpace.entities.items.length,
        selectedClustersID: state.workSpace.clusters.highlightedClusters
    }
};

export default connect(mapStateToProps)(ParamsPanel);
