import React, {useState} from 'react';
import {Form, Header, Icon, Message, Radio, Rating} from "semantic-ui-react";
import {Button} from "../utils/UI";
import {Field, formValueSelector, reduxForm, SubmissionError} from "redux-form";
import CustomModal from "../utils/Modal";
import formComponentGenerator, {
    formInputComponent,
    formRadioComponent, formRatingComponent,
    formTextComponent
} from "../utils/ReduxFormComponentWrapper";
import {connect} from "react-redux";
import {submitFeedback} from "../../store/actions";

const messageTypes = [
    {
        name: 'review',
        text: ' '
    },
    {
        name: 'question',
        text: ' '
    },
    {
        name: 'critical',
        text: '   '
    },
    {
        name: 'warning',
        text: '   '
    },
    {
        name: 'idea',
        text: '  /   /  '
    },
    {
        name: 'offer',
        text: ' / '
    },
];


const submitValidate = (values) => {
    let error = null;
    if (!values.messageType)
        error = '  ';

    if (values.messageType === 'question' && !values.contact)
        error = ' ,     ';

    if (values.messageType === 'offer' && !values.contact)
        error = ' ,      ';

    if (!values.message)
        error = '    ';

    if (error)
        throw new SubmissionError({
            _error: error
        });
};


const AlertModal = (props) => {
    const {handleClose, header, text} = props;

    return (
        <CustomModal handleClose={handleClose}>
            <Header>{header}</Header>
            <div style={{margin: '20px'}}>
                <p style={{fontSize: '1.2rem'}}>{text}</p>
                <Button basic style={{float: 'right', marginBottom: '10px'}} text={''} onClick={handleClose}/>
            </div>
        </CustomModal>
    )
};


const Feedback = (props) => {
    let {handleSubmit, handleClose} = props;
    console.log('props', props);
    const [visibleAlert, setVisibleAlert] = useState(false);
    const [successMessage, setSuccessMessage] = useState('');

    const onSubmit = (values) => {
        submitValidate(values);
        setVisibleAlert(true);
        let message = '  !';

        if (values.messageType === 'question')
            message = '     !';
        if (values.messageType === 'review')
            message = '  !';
        if (values.messageType === 'warning')
            message = '     !';
        if (values.messageType === 'critical')
            message = '      !';
        if (values.messageType === 'offer')
            message = '      !';
        if (values.messageType === 'idea')
            message = '   .        !';

        props.onSubmit(values);
        setSuccessMessage(message);
    };

    handleSubmit = handleSubmit(onSubmit);
    return (
        <>
            {
                visibleAlert ?
                    <AlertModal handleClose={handleClose}
                                header={' '}
                                text={successMessage}/> :
                    <CustomModal handleClose={handleClose}>
                        <Header> </Header>
                        {
                            props.error &&
                            <Message
                                style={{margin: '10px'}}
                                error
                            >{props.error}</Message>
                        }
                        <div style={{margin: '30px'}}>
                            <Form>
                                <Form.Field>
                                    <label> </label>
                                </Form.Field>
                                {
                                    messageTypes.map(messType => (
                                        <Field
                                            name={'messageType'}
                                            key={messType.name}
                                            type={'radio'}
                                            component={formRadioComponent}
                                            label={messType.text}
                                            value={messType.name}
                                        />
                                    ))
                                }
                                <Field component={formInputComponent}
                                       label={'     '}
                                       name={'contact'}
                                       placeholder={'  example@gmail.com'}
                                />
                                {
                                    props.messageType === 'review' &&
                                    <>
                                        <Form.Field>
                                            <label> </label>
                                        </Form.Field>
                                        <Field
                                            name={'rating'}
                                            component={formRatingComponent}
                                        />
                                    </>
                                }

                                <Field
                                    name={'message'}
                                    component={formTextComponent}
                                    label={' '}
                                />
                                <Button onClick={handleSubmit} basic text={''}/>
                            </Form>
                        </div>
                    </CustomModal>
            }
        </>
    )
};

const mapDispatchToProps = ({
    onSubmit: submitFeedback
});

const selector = formValueSelector('Feedback');

const mapStateToProps = state => {
    return {
        messageType: selector(state, 'messageType')
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(
    reduxForm(
        {
            form: 'Feedback',
            forceUnregisterOnUnmount: true
        }
    )(Feedback));
