const keyWords = new Set(['group', 'feed', 'news', 'music', 'edit', 'settings', 'im', 'friends', 'groups', 'video', 'apps', 'vkpay', 'market', 'docs', 'blog', 'dev', "biz", "about", "jobs", "legal", "data_protection", "safety", "exilemusic", "exilemusic", "verify", "exile_music"]);

export const extractFromString = (values) => {
    return (dispatch, getState) => {
        const text = values.text;
        if (!text) {
            throw new SubmissionError({text: ' '});
        }
        const regex = /href="\/(\w+)"/g;
        let match = regex.exec(text);
        if (!match) {
            throw new SubmissionError({text: '   '});
        }
        let matches = new Set();
        while (match) {
            matches.add(match[1]);
            match = regex.exec(text);
        }
        matches = [...matches.difference(keyWords)];
        const queryString = queryCreator('vk.users', matches);
        dispatch(executeQuery(queryString));
    }
};

export const downloadWorkSpace = () => {
    return (dispatch, getState) => {
        const filename = `NTrail workspace (${getState().workSpace.clusters.items.length} clusters) version ${getState().workSpace.version}`;
        download(JSON.stringify(getState()), filename, '')
    }
};

export const uploadWorkSpace = () => {
    return (dispatch, getState) => {
        const input = document.createElement('input');
        input.type = 'file';
        input.onchange = e => {
            const file = e.target.files[0];

            const reader = new FileReader();
            reader.readAsText(file, 'UTF-8');

            reader.onload = readerEvent => {
                const content = readerEvent.target.result;
                console.log('File loaded');
                try {
                    const state = JSON.parse(content);
                    if (Number(state.workSpace.version.split('.')[0]) !== Number(version.split('.')[0])) {
                        alert('       .    ');
                    }
                    saveState(state);

                } catch (e) {
                    alert('    NTrail ! (   ,      "  ")    ')
                }
                window.location.reload(true);
            }
        };
        input.click();
    }
};

const transformEntities = (dispatch, getState, setTransformFunc) => {
    const state = getState();
    const selectedClusters = state.workSpace.clusters.highlightedClusters;
    if (selectedClusters.length >= 2) {
        const selectedClustersEntities = selectedClusters.map(
            id => state.workSpace.objects.clusters[id].entities
        );
        let resultEntities = new Set(selectedClustersEntities[0]);
        for (let entities of selectedClustersEntities.slice(1)) {
            resultEntities = resultEntities[setTransformFunc](new Set(entities));
        }
        const entitiesID = [...resultEntities];
        if (entitiesID.length === 0) {
            dispatch(showMessage(' ', '  ', false, true))
        } else {
            const queryString = queryCreator('vk.users', entitiesID);
            dispatch(executeQuery(queryString));
        }
    }
};

export const intersectClusters = () => {
    return (dispatch, getState) => {
        transformEntities(dispatch, getState, 'intersect')
    }
};

export const unionClusters = () => {
    return (dispatch, getState) => {
        transformEntities(dispatch, getState, 'union')
    }
};

export const subtractClusters = () => {
    return (dispatch, getState) => {
        transformEntities(dispatch, getState, 'difference')
    }
};

export const symDiffClusters = () => {
    return (dispatch, getState) => {
        transformEntities(dispatch, getState, 'symmetricDifference')
    }
};

export const pickOutToCluster = () => {
    return (dispatch, getState) => {
        const state = getState();
        const selectedEntities = state.workSpace.entities.selectedItems;
        if (selectedEntities.length > 0) {
            const queryString = queryCreator(getCurrentClusterPrefix(state), selectedEntities);
            dispatch(executeQuery(queryString, getSelectedClusterID(state)));
        }
    }
};

export const deleteFromCluster = () => (dispatch, getState) => {
    const state = getState();
    const selectedEntities = state.workSpace.entities.selectedItems;
    const allEntities = state.workSpace.entities.items;
    const subtractedEntities = [...new Set(allEntities).difference(new Set(selectedEntities))];
    if (selectedEntities.length > 0) {
        const queryString = queryCreator('vk.users', subtractedEntities);
        dispatch(executeQuery(queryString));
    }
};
const getClustersChildren = (state, ids) => {
    if (ids.length === 0)
        return [];
    const children = getClusters(state).filter(cluster => ids.includes(getClusterParent(cluster))).map(cluster => cluster.id).flat();
    console.log('children-', children)
    return children.concat(getClustersChildren(state, children));
};
export const deleteSelectedCluster = () => {
    return (dispatch, getState) => {
        const state = getState();
        let selectedClusters = [state.workSpace.clusters.selectedClusterID];
        if (state.workSpace.clusters.highlightedClusters.length > 0)
            selectedClusters = state.workSpace.clusters.highlightedClusters;
        console.log('selectedClusters', selectedClusters);
        let clustersToDelete = selectedClusters;
        if (selectedClusters.length === 1) {
            console.log('1 selected cluster')
            const childrenClusters = getClustersChildren(state, selectedClusters);
            console.log('childrenClusters', childrenClusters)

            if (childrenClusters.length > 0)
                clustersToDelete = childrenClusters;


        }
        console.log(clustersToDelete.length, clustersToDelete);
        dispatch(removeClusters(clustersToDelete));
        if (clustersToDelete.includes(getActiveClusterID(state))) {
            const remainClusters = getClustersIDS(state).filter(id => !clustersToDelete.includes(id));
            if (remainClusters.length === 0)
                dispatch(clearAll());
            else
                dispatch(selectCluster(getClustersIDS(state).filter(id => !clustersToDelete.includes(id))[0]));
        }
    }
};

export const submitFeedback = (values) => {
    return (dispatch, getState) => {
        const state = getState();
        const feedbackData = {
            values: values,
            env: getWorkingEnvironmentSettings(state)
        };

        console.log('FeedBack', values);
        const alarmerMessage = `NTrail feedback:%0Atype: ${values.messageType}%0Acontact: ${values.contact}%0Arate: ${values.rating}%0Amessage: ${values.message}`;
        sendAlarmerMessage(alarmerMessage);
        executeFeedbackQuery(feedbackData);
    }
};

