export const executeClusterAction = (action) => {
    return (dispatch, getState) => {
        const state = getState();
        console.log('EXECUTE CLUSTER ACTION', action);
        const currPrefix = getCurrentClusterPrefix(state);
        console.log('currPrefix', currPrefix);
        let entitiesIDS = getSelectedItemsIDS(state);
        let queryString;
        if (entitiesIDS.length > 0) {
            queryString = queryCreator(currPrefix, entitiesIDS, action.value);
        } else {
            queryString = getCurrentClusterQuery(state) + ' ' + action.value;
        }

        dispatch(executeQuery(queryString, getSelectedClusterID(state)));
    }
};
