import React, {useEffect, useState} from 'react';
import {
    Sigma,
    RandomizeNodePositions,
    ForceAtlas2, SigmaEnableWebGL
} from 'react-sigma';
import {connect} from "react-redux";
import {colorByNumber, listHash} from "../../utils";
import {getEntities, getEntitiesColors} from "../../store/selectors";
import {Icon, Image} from "semantic-ui-react";
import Link from "../utils/Link";
import {selectCluster} from "../../store/clustersReducer";
import {Button, UIGroup} from "../utils/UI";

const getNodeColorAndSize = (entity, selectedEntities, visibleEntities, entitiesColors) => {
    let size = 5;
    if (selectedEntities.includes(entity.id))
        size = 8;
    return {
        size: size,
        color: colorByNumber(entitiesColors[entity.id].id)
    };
    let color = entity.properties.sex === 1 ? '#ff5555' : '#5555ff';
    if (!entity.valid)
        color = '#aaa';
    if (!visibleEntities.includes(entity.id)) {
        // color = LightenDarkenColor(color, -40);
        color = '#777';
        size = 4
    }
    if (selectedEntities.includes(entity.id)) {
        color = '#0a0';
    }

    return {
        color: color,
        size: size
    };
};

class UpdateNodeProps extends React.Component {

    componentWillReceiveProps({sigma, selectedEntities, visibleEntities, entitiesColors}) {

        sigma.graph.nodes().forEach(n => {
            const updateNode = {
                ...getNodeColorAndSize(n.entity, selectedEntities, visibleEntities, entitiesColors),
            };
            Object.assign(n, updateNode)
        });
        sigma.refresh();

    }

    render = () => null
}


const GraphActiveHint = (props) => {
    const {x, y, entity, onClick} = props;

    const style = {
        left: x - 55,
        top: y - 150
    };
    return (
        <div className={'graphActiveEntity'} style={style} onClick={e => {
            e.stopPropagation();
            onClick(entity)
        }}>
            <Image src={entity.img} size='tiny' circular style={{marginBottom: '0.5rem'}}/>
            <Link text={entity.name} url={entity.url}/>
        </div>
    )
};

const GraphSelectedEntity = (props) => {
    const {entity, setSelectedEntity, highlightColor, selectCluster} = props;

    if (!entity) return null;
    return (
        <div className={'graphActiveEntity'} style={{position: 'absolute', left: 10, top: 10}}>
            <Icon onClick={() => setSelectedEntity(null)} link style={{position: 'absolute', top: 0, right: 20}}
                  name={'close'}/>
            <Image src={entity.img} size='tiny' circular style={{marginBottom: '0.5rem'}}/>
            <Link style={{marginBottom: '1rem'}} text={entity.name} url={entity.url}/>
            <UIGroup>
                <Button color={colorByNumber(entity.colorCluster.id)}
                        onClick={() => selectCluster(entity.colorCluster.id)}
                        text={''}/>
                <Button color={colorByNumber(entity.colorCluster.id)} onClick={() => highlightColor(entity.id)}
                        text={' '}/>
            </UIGroup>
        </div>

    )
};


const EntitiesGraph = (props) => {
    const {selectedEntities, connections, toggleItemSelection, entities, entitiesColors} = props;
    const visibleEntitiesIDS = props.visibleEntities.map(item => item.id);
    const [activeEntity, setActiveEntity] = useState(null);
    const [activeEntityPos, setActiveEntityPos] = useState({});
    const [selectedEntity, setSelectedEntity] = useState(null);

    useEffect(() => {
        setActiveEntity(null);
        setSelectedEntity(null);
    }, [listHash(entities)]);

    const generateGraph = () => {
        return {
            nodes: entities.map(entity => ({
                id: entity.id,
                // label: entity.name,
                ...getNodeColorAndSize(entity, selectedEntities, visibleEntitiesIDS, entitiesColors),
                entity: {...entity, colorCluster: entitiesColors[entity.id]}
            }))
            ,
            edges: Object.entries(connections).map(([firstID, connectedIDS]) =>
                connectedIDS.map(secondID => ({
                    id: firstID + secondID,
                    source: firstID,
                    target: secondID,
                }))).flat()
        }
    };

    const highlightColor = (id) => {
        const color = entitiesColors[id];
        const sameColorEntities = entities.filter(entity => entitiesColors[entity.id] === color).map(item => item.id);
        props.selectEntities(sameColorEntities);
    };

    const graphSettings = {
        defaultNodeColor: '#ec5148',
        defaultEdgeColor: '#ccc',
        edgeColor: 'default',
        drawEdges: true,
        labelThreshold: 100,
        minNodeSize: 0,
        maxNodeSize: 0
    };

    const graphStyle = {
        height: '700px'
    };

    const selectNode = entity => {
        if (!entity) {
            setActiveEntity(null);
            setSelectedEntity(null);
            return;
        }
        setSelectedEntity(entity);
        toggleItemSelection(entity.id);
    };

    const onClickNode = (node) => {
        selectNode(node.entity);
    };

    const onOverNode = (node) => {
        setActiveEntityPos({x: node['cam0:x'], y: node['cam0:y']});
        setActiveEntity(node.entity)
    };

    const onOutNode = (node) => {
        setActiveEntity(null)
    };

    const graphID = entities.reduce((acc, val) => acc + val.id, '').hashCode();

    return (
        <div
            key={graphID}
        >
            <Sigma
                renderer="webgl"
                graph={generateGraph()}
                settings={graphSettings}
                style={graphStyle}
                onClickNode={(e) => {
                    onClickNode(e.data.node)
                }}
                onOverNode={(e) => onOverNode(e.data.node)}
                onOutNode={(e) => onOutNode(e.data.node)}
            >
                <SigmaEnableWebGL/>
                <RandomizeNodePositions>
                    <ForceAtlas2 worker
                                 scalingRatio={1}
                                 slowDown={10}
                                 linLogMode={false}
                                 iterationsPerRender={1}
                                 timeout={entities.length / 100 * 1000}
                    />

                    <UpdateNodeProps entitiesColors={entitiesColors}
                                     visibleEntities={visibleEntitiesIDS}
                                     selectedEntities={selectedEntities}/>
                </RandomizeNodePositions>
            </Sigma>
            {
                activeEntityPos && activeEntity &&
                <GraphActiveHint x={activeEntityPos.x} y={activeEntityPos.y} entity={activeEntity}
                                 onClick={onClickNode}/>
            }
            {
                selectedEntity &&
                <GraphSelectedEntity
                    highlightColor={highlightColor}
                    setSelectedEntity={selectNode}
                    selectCluster={props.selectCluster}
                    entity={selectedEntity}/>
            }
        </div>
    )
};


const mapStateToProps = (state) => {
    return {
        connections: state.workSpace.entities.connections,
        entities: getEntities(state),
        entitiesColors: getEntitiesColors(state)
    }
};

const mapDispatchToProps = ({
    selectCluster
});

export default connect(mapStateToProps, mapDispatchToProps)(EntitiesGraph);
