const mapStateToProps = state => {
    return {
        selectedClustersCount: state.workSpace.clusters.highlightedClusters.length,
        selectedEntitiesCount: state.workSpace.entities.selectedItems.length,
        haveClusters: state.workSpace.clusters.items.length > 0,
        currentClusterEntitiesCount: getEntitiesItemsIDS(state).length,
        clusterActions: getActiveClusterID(state) && getCurrentClusterData(state).actions,
    }
};
const mapDispatchToProps = {
    executeClusterAction,
    downloadWorkSpace,
    uploadWorkSpace,
    intersectClusters,
    unionClusters,
    subtractClusters,
    symDiffClusters,
    pickOutToCluster,
    deleteSelectedCluster,
    deleteFromCluster
};

export default connect(mapStateToProps, mapDispatchToProps)(ActionsPanel)