import {createSelector} from "reselect";
import {store} from '././store'
import {shuffle} from "../utils";

const getState = () => store.getState();

export const getObj = (objType, idx) => {
    if (idx === null) return undefined;
    return getState().workSpace.objects[objType][idx]
};

export const getObjects = (objType, indexes) => {
    return indexes.map(id => getObj(objType, id))
};

export const getEntitiesItemsIDS = state => state.workSpace.entities.items;

export const getClustersIDS = state => state.workSpace.clusters.items;

export const getSelectedItemsIDS = state => state.workSpace.entities.selectedItems;

export const getActiveClusterID = state => state.workSpace.clusters.selectedClusterID;

export const getCurrentClusterPrefix = state => getCurrentClusterData(state).params.prefix;

export const getCluster = (state, clusterID) => getObj('clusters', clusterID);

export const getClusterParent = cluster => {
    if (cluster.parent)
        return cluster.parent;
    return null;
};

export const getSelectedEntitiesURLS = createSelector(
    getSelectedItemsIDS,
    selectedItems => getObjects('entities', selectedItems).map(item => item.url)
);

export const getCurrentClusterData = state => {
    const id = getActiveClusterID(state);
    return getObj('clusters', id);
};

export const getCurrentClusterQuery = state => {
    const id = getActiveClusterID(state);
    return getObj('clusters', id).params.query;
};

export const getEntities = createSelector(
    getEntitiesItemsIDS,
    (entitiesItems) => {
        return getObjects('entities', entitiesItems);
    },
);

export const getClustersData = createSelector(
    getClustersIDS,
    clustersIDS => {
        return getObjects('clusters', clustersIDS)
    }
);

export const getClusters = createSelector(
    getClustersData,
    clustersData => {
        return clustersData.map(
            data => {
                const entitiesID = data.entities.items;
                let randomIDS = [...entitiesID].slice(0, 9);
                shuffle(randomIDS);
                const photos = getObjects('entities', randomIDS)
                    .filter(item => item && item.valid && item.img)
                    .slice(0, 3).map(item => item.img);
                return {
                    ...data.params,
                    photos: photos,
                    count: entitiesID.length
                }
            }
        )
    }
);

export const getClustersTreeDict = createSelector(
    getClusters,
    (clusters) => {
        const clustersTree = {};
        for (let cluster of clusters) {
            const parent = getClusterParent(cluster);
            if (!(parent in clustersTree))
                clustersTree[parent] = [];
            clustersTree[parent].push(cluster);
        }
        return clustersTree;
    }
);

export const getEntitiesColors = createSelector(
    [getClustersData],
    (clustersData) => {
        const entitiesColors = {};
        clustersData = clustersData.slice();
        const compare = (a, b)=>{
            if(a.entities.items.length < b.entities.items.length)
                return 1;
            else if(a.entities.items.length > b.entities.items.length)
                return -1;
            return 0
        };
        clustersData.sort(compare);
        for (let cluster of clustersData) {
            for (let entityID of cluster.entities.items) {
                entitiesColors[entityID] = cluster.params;
            }
        }

        return entitiesColors;
    }
);