import React from 'react';
import EntityItem from "./EntityAvatar/Entity";
import {connect} from "react-redux";
import Scrolling from "../utils/Scrolling";


const ConnectionItem = props => {
    const entity = props.entity;
    const first = entity.items[0];
    const second = entity.items[1];
    return (
        <>
            <div className={'entityConnectionItem'}>
            <EntityItem entity={first} toggleSelection={() => props.toggleItemSelection(first.id)}
                        selected={props.selectedEntities.includes(first.id)}/>
            <EntityItem entity={second} toggleSelection={() => props.toggleItemSelection(second.id)}
                        selected={props.selectedEntities.includes(second.id)}
            />
            </div>
        </>
    )
};


const EntitiesConnections = (props) => {
    const visibleEntities = props.visibleEntities;
    return (
        <Scrolling>
        {
            visibleEntities.map(item => (
                <ConnectionItem entity={item} {...props}/>
            ))
        }
        </Scrolling>
    )
};


export default EntitiesConnections;
