import React from "react";
import {Dropdown} from "semantic-ui-react";
import {connect} from "react-redux";
import StringSearch from "./StringSearch";
import {changeFilterMode} from "../../../store/controlsReducer";
import ServerSearch from "./ServerSearch";


const PageSearch = props => {
    return (
        <></>
    )
};


const FilterPanel = ({searchMode, ...props}) => {
    const searchModeOptions = [
        {key: 'net', text: '  (  API  )', value: 'net'},
        // {key: 'cache', text: '   NTrail ( )', value: 'cache'},
        // {key: 'page', text: '   (   )', value: 'page'},
        {key: 'text', text: '   ', value: 'text'},
    ];
    return (
        <div
            style={{marginTop: '1rem'}}>
            <b>  </b>
            <Dropdown
                value={searchMode}
                onChange={(e, data) => props.changeFilterMode(data.value)}
                fluid selection
                options={searchModeOptions}
                style={{marginBottom: '1rem'}}
            />
            {
                searchMode === 'text' &&
                <StringSearch/>
            }
            {
                (searchMode === 'net' || searchMode === 'cache') &&
                <ServerSearch/>
            }
            {
                (searchMode === 'page') &&
                <PageSearch/>
            }
        </div>
    );
};

const mapStateToProps = state => {
    return {
        searchMode: state.workSpace.controls.filterMode
    }
};
const mapDispatchToProps = ({
    changeFilterMode
});


export default connect(mapStateToProps, mapDispatchToProps)(FilterPanel)
