import React, {useEffect, useState} from 'react';
import ClusterLine from "./ClusterLine";
import {connect} from "react-redux";
import {getClusterParent, getClustersTreeDict} from "../../store/selectors";

const getAllChildrenIDS = (clustersTree, parentID, withParent = false) => {
    if (!(parentID in clustersTree)) {
        if (withParent)
            return [parentID];
        return [];
    }

    return clustersTree[parentID].map(child => getAllChildrenIDS(clustersTree, child.id, true)).flat();
};

const ClustersTree = (props) => {
    let {clusters, clustersID, parentID, level, selectedClusterID, selectCluster, clustersTree} = props;
    if (!level)
        level = 0;
    let currLevelClusters = clustersTree[parentID];

    const [openedClusters, setOpenedClusters] = useState(currLevelClusters.map(item=>item.id));


    const toggleOpen = (clusterID) => {
        if (openedClusters.includes(clusterID))
            setOpenedClusters(openedClusters.filter(item => item !== clusterID));
        else
            setOpenedClusters([...openedClusters, clusterID]);
    };

    return (
        <div>
            {
                currLevelClusters.map(cluster => (
                    <div key={cluster.id}>
                        <ClusterLine
                            indent={level}
                            selectCluster={selectCluster}
                            cluster={cluster}
                            selected={cluster.id === selectedClusterID}
                            opened={openedClusters.includes(cluster.id)}
                            toggleOpen={() => toggleOpen(cluster.id)}
                            haveChildren={clusters.filter(child => getClusterParent(child) === cluster.id).length > 0}
                        />
                        {
                            openedClusters.includes(cluster.id) && clustersTree[cluster.id] &&
                            <ClustersTree
                                {...props} level={level + 1}
                                parentID={cluster.id}
                            />
                        }
                    </div>
                ))
            }
        </div>
    )
};




const ClustersTreeWrapper = (props) => {

    return (
        <div style={{maxHeight: '70vh', overflow: 'auto', padding: '3px'}}>
            <ClustersTree {...props} parentID={null}/>
        </div>
    )
};


const mapStateToProps = (state, props) => {

    return {
        clustersTree: getClustersTreeDict(state)
    }
};

const mapDispatchToProps = ({});

export default connect(mapStateToProps, mapDispatchToProps)(ClustersTreeWrapper);
