import {Button, Header, Icon} from "semantic-ui-react";
import React from "react";
import {ActionPanelButton} from "./ActionsPanel";
import {connect} from "react-redux";
import {deleteFromCluster, executeClusterAction, pickOutToCluster} from "../../../store/actions";
import {
    getActiveClusterID,
    getCurrentClusterData,
    getEntitiesItemsIDS
} from "../../../store/selectors";

const ActiveActions = props => {
    if(!props.actions)
        return <></>;
    let header = props.header;
    let selectedEntities = props.selectedEntitiesCount;
    if (!selectedEntities)
        selectedEntities = props.currentClusterEntitiesCount;


    let actions = [];
    for (let action of props.actions) {
        const selectionTarget = action.target;
        if (!selectionTarget || selectionTarget === 'any')
            actions.push(action);
        else if (selectedEntities === 1 && selectionTarget === 'one')
            actions.push(action);
        else if (selectedEntities > 1 && selectionTarget === 'many')
            actions.push(action);
        else if (props.selectedEntitiesCount === 0 && selectionTarget === 'cluster')
            actions.push(action);
    }
    return <>
        {
            (actions.length > 0 || props.selectedEntitiesCount > 0) &&
            <>{
                header &&
                <Header size={'small'} style={{marginLeft: props.headerMargin}}> </Header>

            }{
                actions.length > 0 &&
                <Button.Group vertical basic style={{width: '100%', marginBottom: '1rem'}}>
                    {
                        actions.map(action => (

                            <ActionPanelButton key={action.id}
                                               onClick={() => props.executeClusterAction(action)} text={action.name}
                                               icon={<Icon name={'object ungroup outline'}/>}/>
                        ))
                    }
                    {
                        props.selectedEntitiesCount > 0 && props.selectedEntitiesCount !== props.currentClusterEntitiesCount &&
                        <>
                            <ActionPanelButton onClick={props.pickOutToCluster} text={'   '}
                                               icon={<Icon name={'dot circle outline'}/>}/>
                            <ActionPanelButton onClick={props.deleteFromCluster} text={'  '}
                                               icon={<Icon name={'remove circle'}/>}/>
                        </>
                    }
                </Button.Group>
            }
            </>
        }
    </>
};

const mapStateToProps = state => {
    return {
        selectedClustersCount: state.workSpace.clusters.highlightedClusters.length,
        selectedEntitiesCount: state.workSpace.entities.selectedItems.length,
        haveClusters: state.workSpace.clusters.items.length > 0,
        currentClusterEntitiesCount: getEntitiesItemsIDS(state).length,
        actions: getActiveClusterID(state) && getCurrentClusterData(state).actions,
    }
};
const mapDispatchToProps = ({
    executeClusterAction,
    pickOutToCluster,
    deleteFromCluster
});

export default connect(mapStateToProps, mapDispatchToProps)(ActiveActions)